.k-grid-table {
    td:nth-child(2){
        text-overflow:initial;
    }
    .k-dropdown-button.k-i-more-vertical {
        background:transparent;
        &:before {
            display: none;
        }

        .k-button {
            width: 1em;
            height: 1em;
            -moz-osx-font-smoothing: grayscale;
            -webkit-font-smoothing: antialiased;
            font-size: 16px;
            font-family: 'WebComponentsIcons';
            font-style: normal;
            font-variant: normal;
            font-weight: normal;
            line-height: 1;
            speak: none;
            text-transform: none;
            text-decoration: none;
            display: inline-block;
            background: transparent;
            padding:0;
            &:before {
                content: '\e031';

                opacity: 1;
                display: block;
                background: transparent;
            }
        }
    }
}