.contractPanel .k-content.p-4 {
    padding-bottom: 5px !important;
}

.contract {
    &-details {

        @at-root .project-details .breadcrumb,
        & .breadcrumb {
            background: $white;
        }


    }

    &-header {
        background: $white;
        margin: 0 -15px 30px;
        padding: 0 15px 5px;
        box-shadow: 0 15px 15px -15px rgba(0, 0, 0, 0.1);
    }

    &-title {
        color: $primary;
        cursor: pointer;
        line-height: 1.3;
        display: inline;

        i {
            transition: 0.2s linear;
            font-size: 18px;
            border-left: 1px solid $gray-300;
            padding-left: 8px;
            margin-left: 5px;
            display: inline;
            color: $gray-500;

            &.collapsed {
                color: $gray-500;
            }
        }


    }

    &-info {

        // font-size: 14px;
        .list-inline {
            line-height: 1.6;
        }
    }

    &Panel {
        margin-bottom: 15px;
    }
}

.top-header {
    background: $white;
    margin-bottom: 30px;
    padding-bottom: 15px;
}

.k-panelbar .k-header {
    cursor: pointer !important;
}

.k-tabstrip {
    &-wrapper {
        flex: 1;
    }

    &-prev,
    &-next {
        height: 50px;
    }
}

:root {
    --bg: red;
}

.jobrequest {
    &-status {
        display: flex;
        background: $gray-300;
        margin: 15px -15px;
        padding: 20px;



        li {
            list-style: none;
            display: inline-block;

            font-weight: $fw-b;
            flex-grow: 1;
            position: relative;
            padding-right: 5px;
            height: 25px;
            color: #777;

            &:after {
                content: '';
                width: calc(100% - 10px);
                height: 2px;
                position: absolute;
                left: 0;
                top: calc(50% - 1px);
                background: $gray-400;
                // z-index: -1;
            }

            &:last-child {
                flex: initial;
                padding-right: 0;
            }

            &.active {
                overflow: visible;
                color: darken($primary, 15%);

                >span {
                    overflow: visible;
                }

                .jobrequest-count {
                    background: darken($primary, 15%);
                    color: $gray-300;
                    // height: 30px;
                    // width:30px;
                    // line-height: 30px;

                }
            }

            >span {
                background: $gray-300;
                display: inline-block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                padding-right: 5px;
                position: relative;
                z-index: 1;

            }
        }

    }

    &-count {
        width: 25px;
        height: 25px;
        border-radius: 50%;
        background: $gray-400;
        display: block;
        text-align: center;
        line-height: 25px;
        font-weight: bold;
        color: $gray-600;
        font-size: 14px;
        float: left;
        margin-right: 8px;
    }
}

.uploaded-file {
    a {
        padding: 10px 0;
        display: block;
        text-decoration: none;

        &:hover {
            color: darken($primary, 15%);
        }
    }

    i {
        font-size: 22px;
        margin-bottom: 6px;
    }

    span {
        font-size: 16px;
    }
}

.list-box {
    &-container {
        position: relative;
        padding-bottom:30px;
    }
    &-error{
        position: absolute;
        bottom:0;
        left:0;
        width:100%;
    }
}