$footer-height:60px;
html {
    position: relative;
    min-height: 100%;
}

body {
    padding-top:62px;
    margin-bottom: $footer-height;
    // min-height: calc(100vh - #{$footer-height});
    overflow: auto;
    @include media-breakpoint-down(sm) {
        padding-top:72px;
      }
}

#portageIframe {
    height: calc(100vh - 165px);
}


.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    white-space: nowrap;
    height: calc(#{$footer-height} - 10px);
    line-height: calc(#{$footer-height} - 10px);
    overflow: hidden;
    background: #353535;
    a{
        padding:0 15px;
        text-decoration: none;
        color:$gray-400;
        font-weight: $fw-r;
        &:hover{
            color:$white;
        }
    }
}