.document {
    &-management {
        .k-upload {
            background: transparent;
            border: 0;
        }

        .k-upload-button {
            margin: 0;
        }

        .k-treeview-lines {
            padding-left: 0;

            // >li{
            //     padding-left:0;
            // }
            .k-item.k-treeview-item:hover,
            .k-in:hover,
            .k-in.k-state-hover {
                background: transparent;
                // color:
            }

            .k-in {
                width: 100%;

                &.k-state-selected {
                    width: 100%;
                    background: transparent;
                }
            }
        }

        .pane-content {
            padding: 0;

            &.file-explore {
                padding: 15px;
                background: $gray-200;
            }
        }
    }

    &-manager {
        &-top-header {
            >.col-sm-4 {

                .k-button,
                .k-upload {
                    float: right;
                    margin-left: 10px;
                }

                .k-dropzone-hint,
                .k-upload-status-total {
                    display: none;
                }
            }
        }
    }

}

.explorer {
    &-list {
        padding:5px;
        &, &:focus {
            background: transparent;
            border: none;
            outline: none;
        }
        &:hover{
            background: rgba($white, 0.4);
            // box-shadow: 0 0 15px rgba(0,0,0,0.1);
            border-radius: 4px;
        }

        .k-icon {
            font-size: 40px;
            color: $gray-600;

        }

        small {
            margin-top: 8px;
            display: block;
            word-break: break-all;
        }
    }
    &-button{
        padding: 20px;
        align-items: center;
        justify-content: flex-start;
        display: flex;
        flex-flow: column wrap;
    }
}
.file-path{
    text-align:right;
    background: $gray-200;
    padding: 7px 15px;
    border-radius: 4px;
    display: inline-block;
}

.dir-separator {
    display: inline-block;
    color: #999;
    font-weight: $fw-b;

    a {
        color: #999;

        &:hover {
            text-decoration: none;
            color: #555;
        }
    }
}