.file {
    &-upload-container {
        background: #f5f5f5;
        margin-top: -20px;
        padding: 20px;
        margin-bottom: 30px;
        border-bottom: 1px solid #e5e5e5;
    }

    &-download {
        border: 1px solid $gray-300;
        border-radius: 4px;
        padding: 5px 15px 5px 5px;
        display: flex;
        align-items: center;
        width: 240px;
        margin: 0 auto 20px;
        background: $white;


        &:hover {
            text-decoration: none;
            background: $gray-200;
        }

        i {
            font-size: 24px;

            &.material-icons {
                justify-self: flex-end;
                font-size: 18px;
                color: $success;
            }
        }

        p {
            margin-left: 5px;
            color: $primary;
            font-weight: $fw-b;
            font-size: 13px;
        }

        >span {
            display: flex;
            align-items: center;

            &:first-child {
                width: 100%;
            }
        }

    }

    &-upload {
        i {
            font-size: 22px;
            margin-right: 10px;
            float: left;
        }

        &-text {
            font-size: 14px;
        }


        &-input {
            display: none;
        }
    }

    &-name {
        display: block;
        color: $primary;
        font-weight: 500;
        margin-top: 5px;

        .badge {
            padding: 5px 12px;
            font-size: 12px;
        }
    }
}

.divider-or {
    position: relative;
    text-align: center;
    width: 100%;
    margin: 20px 0;

    &:after {
        content: '';
        height: 2px;
        width: 50%;
        position: absolute;
        left: 25%;
        top: 50%;
        background: $gray-300;
    }

    span {
        background: #f5f5f5;
        padding: 0 10px;
        font-weight: $fw-b;
        position: relative;
        z-index: 1;
    }
}


.k-dropzone {
    @at-root &-active &,
    &-hint {
        display: none !important;
    }

    &:hover,
    &-active {
        background: #e8f7ff !important;

        &:before {
            color: darken($primary, 15%);
        }
    }

    &:not(.document-manager) {
        position: relative;
        text-align: center;
        height: 120px;
        transition: 0.2s linear;

        &:after {
            content: "Drag & Drop or browse your file to upload";
            position: absolute;
            top: 75px;
            font-size: 14px;
            width: 100%;
            font-weight: $fw-b;
            pointer-events: none;
        }

        &:before {
            position: absolute;
            top: 15px;
            width: 100%;
            text-align: center;
            font-family: 'Material Icons';
            content: '\e864';
            font-size: 56px;
            line-height: 56px;

        }
    }
}

.k-upload {
    border: 1px dashed #ccc;
    transition: 0.2s linear;
    border-radius: 3px;
    margin-bottom:20px;

    &-button {
        margin: 0 !important;
        width: 100%;
        height: 120px;
        position: absolute;
        top: 0;
        opacity: 0;

        input {
            height: 100% !important;
            top: 0;
        }

        >span {
            display: none;
        }

    }

    &-status {
        display: none !important;
    }

    +.k-grid {
        margin-bottom: 20px;
    }
}

.uploaded-grid {
    a {
        font-weight: $fw-b;
        color:$primary;
    }

    td:last-child {
        text-align: right;
    }
}
