.navbar {
    background-color: $primary;
    padding-top: 0;
    padding-bottom: 0;

    @include media-breakpoint-down(md) {
        // padding-top: 5px;
        // padding-bottom: 5px;

        .menu-large.show {
            background: $navbar-active;
        }

        &-collapse {
            margin-left: -16px;
            margin-right: -15px;
            margin-top: 10px;

            flex-basis: calc(100% + 30px);
        }

        &-right {
            position: absolute;
            right: 90px;
            top: 23px;

            @include media-breakpoint-down(md) {
                right: 85px;
                top: 7px;
            }
        }

        .nav-link {
            padding-left: 15px;
            padding-right: 35px;
        }
    }

    .nav-item {
        &:hover {
            color: rgba(255, 255, 255, 0.5);
            background: $navbar-active;
        }
    }

    &-right {
        display: flex;

        li>a {
            line-height: 20px;

            display: block;
            color: #fff;
            line-height: 1;

            &:not(.nav-profile) {
                padding: 15px;
            }

            &:hover {
                color: rgba(255, 255, 255, 0.7)
            }
        }

        .dropdown-menu {
            padding: 0;
            
        }

        .dropdown-toggle:after {
            display: none;
        }

    }

   


}

.nav-profile {
    padding: 0 0 0 15px;

    &-links {
        .dropdown-item {
            display: flex;
            padding-top: 8px;
            padding-bottom: 8px;
        }

        i {
            opacity: 0.7;
            font-size: 18px;
            margin-right: 10px;
        }
    }

    &-image {
        background: url('../img/user.svg') #fff center no-repeat;
        background-size: 27px;
        margin-top: 10px;
        width: 35px;
        height: 35px;
        border-radius: 50%;
        display: block;
    }
}



.nav-link {
    font-weight: $fw-b;
    text-transform: uppercase;
    font-size: 14px;
    color: $white;

    &:after {
        margin-left: 10px;
    }

    &:hover {
        color: $white;
    }

    @include media-breakpoint-down(md) {
        position: relative;
        padding-left: 15px;

        &:after {
            position: absolute;
            right: 20px;
            top: 50%;
        }
    }
}



@include media-breakpoint-up(lg) {
    .navbar-expand-lg .navbar-nav .nav-link {
        padding: 20px 25px;
    }
}


.navbar-toggler {
    &:focus {
        outline: none;
    }

    i {
        font-size: 36px;
    }
}